* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.flagCountry{
  display: flex;
  gap:10px;
  align-items:center;
}

.country{
  font-size: 35px;
}

.country-list{
  list-style: none;
  padding: 0;
}

.country-list__item{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;

  height: 50px;
} 